<script>
  import { createEventDispatcher } from "svelte";
  import Button from "@smui/button";

  const dispatch = createEventDispatcher();

  export let exibit = {
    id: "1",
    img: "penny-black.jpg",
    name: "Penny Black",
    year: 1840,
    country: "UK",
    catPrice: 3000,
    currentBid: 2000,
    isSold: false,
    isOnSale: false
  };

  let bidInputValue;

  $: console.log("exibit", exibit);

  function onClick(ev) {
    dispatch("submitBid", {
      userId: "1",
      id: exibit.id,
      bid: bidInputValue.value ? parseInt(bidInputValue.value) : undefined
    });
  }
</script>

