<div class="container">
  <div>
    <div class="stamp-wrap">
      <img src={`./stamps/${exibit.img}`} />
      {#if exibit.isSold}
        <div class="sold-sign">
          <div class="sold-sign-x">x</div>
          <div class="sold">Sold</div>
        </div>
      {/if}

      <h3>
        {exibit.name}
        <span>({exibit.year})</span>
      </h3>
      <div class="details">
        <div class="line">
          <span class="caption">Country:</span>
          <span>{exibit.country}</span>
        </div>
        <div class="line">
          <span class="caption">Cat Price:</span>
          <span>{exibit.catPrice}$</span>
        </div>
        <div class="line bid">
          <span class="caption">Current Bid:</span>
          <span>{exibit.currentBid}$</span>
        </div>
      </div>
      <div class="toolbar">
        <input type="number" bind:this={bidInputValue} value={exibit.currentBid}/>
        <Button variant="raised" on:click={onClick}>click</Button>
      </div>
    </div>

  </div>

