<script>
  import { createEventDispatcher } from "svelte";
  import Button from "@smui/button";

  const dispatch = createEventDispatcher();

  export let exhibit = {
    item: {
      country: "UK",
      img: "penny-black.jpg",
      name: "Penny Black",
      year: 1840
    },
    id: "1",
    catPrice: 3000,
    currentBid: 2000,
    isSold: false,
    isOnSale: false
  };

  let bidInputValue;

  $: console.log("exibit", exhibit);

  function onClick(ev) {
    dispatch("submitBid", {
      userId: "1",
      id: exhibit.id,
      bid: bidInputValue.value ? parseInt(bidInputValue.value) : undefined
    });
  }
</script>

